@charset "UTF-8";
.whatsapp_for_wordpress_vi-admin-inner {
  padding: 0 60px 0 20px;
}
.whatsapp_for_wordpress_vi-admin-title h1 {
  display: flex;
  align-items: center;
}
.whatsapp_for_wordpress_vi-admin-title h1 span svg {
  width: 50px;
  height: 50px;
}
.whatsapp_for_wordpress_vi-admin-main-basicsettings input:not(input[type=submit]) {
  height: 42px;
  width: 100%;
  border-color: #e8e8e8;
  border-radius: 5px;
  line-height: 40px;
  outline: 0;
  padding-left: 18px;
  padding-right: 30px;
  box-sizing: border-box;
  clear: both;
}
.whatsapp_for_wordpress_vi-admin-main-basicsettings input:not(input[type=submit]):focus {
  border-color: #999;
}
.whatsapp_for_wordpress_vi-admin h2.title {
  font-size: 1.6rem;
  font-weight: 200;
  letter-spacing: 0.7px;
  line-height: 1.8;
  margin: 0;
  margin-top: 0px;
  margin-top: 10px;
}

.card_full {
  max-width: 100%;
}

.hide_this {
  display: none;
}

.whatsapp_for_wordpress_vi-admin-inner {
  padding-left: 0;
}
.whatsapp_for_wordpress_vi-admin-main-config-basic-head {
  border-bottom: 1px solid #444;
  width: 100%;
  padding: 10px 0;
}
.whatsapp_for_wordpress_vi-admin-main-config-basic-head a {
  text-decoration: none;
  font-size: 18px;
  letter-spacing: 0.4px;
  line-height: 1.6;
  color: #111;
  width: 100%;
  display: flex;
  position: relative;
}
.whatsapp_for_wordpress_vi-admin-main-config-basic-head a::after {
  content: "âž•";
  position: absolute;
  right: 0;
  top: 50%;
  transform: translateY(-50%);
}
.whatsapp_for_wordpress_vi-admin-main-config-basic-body {
  display: flex;
  flex-direction: row;
  gap: 40px;
  padding: 40px 0;
}
.whatsapp_for_wordpress_vi-admin-main-config-basic-body-form {
  flex: 1 1 calc(60% - 40px);
  max-width: 60%;
  border: 1px solid #ccc;
  border-radius: 15px;
  padding: 0 20px;
}
.whatsapp_for_wordpress_vi-admin-main-config-basic-body-form input:not(input[type=submit]) {
  height: 42px;
  width: 100%;
  border-color: #e8e8e8;
  border-radius: 5px;
  line-height: 40px;
  outline: 0;
  padding-left: 18px;
  padding-right: 30px;
  box-sizing: border-box;
  clear: both;
}
.whatsapp_for_wordpress_vi-admin-main-config-basic-body-form input:not(input[type=submit]):focus {
  border-color: #999;
}
.whatsapp_for_wordpress_vi-admin-main-config-basic-body-qlook {
  flex: 1 1 calc(40% - 40px);
  max-width: 40%;
  border: 1px solid #ccc;
  border-radius: 15px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
.whatsapp_for_wordpress_vi-admin-main-config-basic-body-qlook-logo {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
.whatsapp_for_wordpress_vi-admin-main-config-basic-body-qlook-logo img {
  max-width: 100%;
  height: auto;
  width: 160px;
}
.whatsapp_for_wordpress_vi-admin-main-config-basic-body-qlook-in h2 {
  font-size: 15px;
  letter-spacing: 0.3px;
  line-height: 1.5;
}

.card_full {
  max-width: 100%;
}

.hide_this {
  display: none;
}

pre {
  background: rgb(65, 34, 34);
  border-left: 5px solid transparent;
  border-right: 5px solid transparent;
  border-top: 1px solid #c9e85d;
  border-bottom: 1px solid #c9e85d;
  padding: 1rem 0;
  color: #fff;
  width: 100%;
  position: relative;
  display: flex;
  justify-content: flex-start;
  flex-direction: column;
}